 .cell-left-num {\n    width: 200px;\n    text-align: center;\n    margin-top: 66px;\n    font-size: 64px;\n    color: rgb(248, 51, 79);\n  }\n  .cell-left-condition {\n    width: 200px;\n    text-align: center;\n    font-size: 24px;\n    color: rgb(109, 109, 109);\n  }\n  .cell-right {\n    position: absolute;\n    right: 0;\n    top: 0;\n    width: 520px;\n    height: 200px;\n    border-left-style: dashed;\n    border-left-width: 2px;\n    border-left-color: rgb(228, 228, 228);\n    border-top-right-radius: 10px;\n    border-bottom-right-radius: 10px;\n    background-color: #ffffff;\n    padding-left: 52px;\n  }\n  .cell-right-scope {\n    margin-top: 60px;\n    font-size: 32px;\n    font-weight: bold;\n    color: rgb(55, 55, 55);\n  }\n  .cell-right-start,\n  .cell-right-end {\n    margin-top: 8px;\n    font-size: 24px;\n    color: rgb(109, 109, 109);\n  }\n</style>\n\n<script>\n  var noList = require('components/weex-no-list/weex-no-list');\n  var waitList = require('components/weex-wait-list/weex-wait-list');\n\n  var stream = weex.requireModule('stream');\n  var dialog = weex.requireModule('shopModal');\n  var shopBase = weex.requireModule('shopBase');\n\n  export default {    \n    components: {\n      noList: noList, \n      waitList: waitList\n    },\n    data: function() {\n      return {\n        listHeight: 300,\n        error_msg: false,\n        noData: false,\n        tip: '',\n        showTip: false,\n        showWait: true,          \n        coupons: [\n          // {\n          //     \"aboutOverTime\": true,\n          //     \"consumptionLimit\": 100,\n          //     \"discountPrice\": 1,\n          //     \"duringTime\": true,\n          //     \"endTime\": 1509010620000,\n          //     \"exchangeRate\": 1,\n          //     \"id\": 1750178,\n          //     \"isAppPlatform\": false,\n          //     \"name\": \"满100元减1元\",\n          //     \"overdue\": true,\n          //     \"platform\": 0,\n          //     \"source\": \"满100-1 测试奖品